// https://dev.to/hexcube/how-to-add-sass-support-to-a-vite-react-app-37p

import axios from "axios";
import Button from "react-bootstrap/Button";
import { useNavigate } from "react-router-dom";

// DragDrop.tsx
import React, {
  useState,
  useCallback,
  useEffect,
  ChangeEvent,
  useRef,
} from "react";
import "./DragDrop.scss";

const DragDrop = () => {
  //       state 
  const [isDragging, setIsDragging] = useState(false);
  const [files, setFiles] = useState([]);
  const [uploadProgress, setUploadProgress] = useState(0);
  const navigate = useNavigate();

  //    ref  (label   )
  const dragRef = useRef(null);

  //     id
  const fileId = useRef(0);

  const onChangeFiles = useCallback(
    (e) => {
      let selectFiles = [];
      let tempFiles = files;

      if (e.type === "drop") {
        selectFiles = e.dataTransfer.files;
      } else {
        selectFiles = e.target.files;
      }

      for (const file of selectFiles) {
        //       ,   append .
        tempFiles = [
          ...tempFiles,
          {
            id: fileId.current++, // fileId  1     .
            object: file, // object     .
          },
        ];
      }

      setFiles(tempFiles);
    },
    [files]
  ); //   files state  deps .

  const handleFilterFile = useCallback(
    (id) => {
      //   id    filter .
      setFiles(files.filter((file) => file.id !== id));
    },
    [files]
  );

  const handleUpload = async () => {
    if (files.length === 0) {
      alert(" .");
      return;
    }

    const formData = new FormData();
    formData.append("pdfFile", files[0].object);
    formData.append("user.id", sessionStorage.getItem("loggedIn"));
    console.log(files[0]);
    const fileName = files[0].object.name;

    try {
      const response = await axios.post("/pdfup1", formData, {
        headers: {
          "Content-Type": "multipart/form-data", // multipart/form-data 
        },
      });

      console.log(" :", response.data);
      navigate(`/make/`, { state: { fileName } });
    } catch (error) {
      console.error(" :", error);
      alert(" !");
    }
  };

  const handleDragIn = useCallback((e) => {
    e.preventDefault();
    e.stopPropagation();
  }, []);

  const handleDragOut = useCallback((e) => {
    e.preventDefault();
    e.stopPropagation();

    setIsDragging(false);
  }, []);

  const handleDragOver = useCallback((e) => {
    e.preventDefault();
    e.stopPropagation();

    if (e.dataTransfer.files) {
      setIsDragging(true);
    }
  }, []);

  const handleDrop = useCallback(
    (e) => {
      e.preventDefault();
      e.stopPropagation();

      onChangeFiles(e);
      setIsDragging(false);
    },
    [onChangeFiles]
  );

  const initDragEvents = useCallback(() => {
    //   4  Listener . ( )

    if (dragRef.current !== null) {
      dragRef.current.addEventListener("dragenter", handleDragIn);
      dragRef.current.addEventListener("dragleave", handleDragOut);
      dragRef.current.addEventListener("dragover", handleDragOver);
      dragRef.current.addEventListener("drop", handleDrop);
    }
  }, [handleDragIn, handleDragOut, handleDragOver, handleDrop]);

  const resetDragEvents = useCallback(() => {
    //   4  Listener . ( )

    if (dragRef.current !== null) {
      dragRef.current.removeEventListener("dragenter", handleDragIn);
      dragRef.current.removeEventListener("dragleave", handleDragOut);
      dragRef.current.removeEventListener("dragover", handleDragOver);
      dragRef.current.removeEventListener("drop", handleDrop);
    }
  }, [handleDragIn, handleDragOut, handleDragOver, handleDrop]);

  useEffect(() => {
    initDragEvents();

    return () => resetDragEvents();
  }, [initDragEvents, resetDragEvents]);

  return (
    <div className="DragDrop">
      <input
        type="file"
        id="fileUpload"
        style={{ display: "none" }} // label   
        multiple={true} //   
        onChange={onChangeFiles}
      />

      <label
        className={isDragging ? "DragDrop-File-Dragging" : "DragDrop-File"}
        //         
        htmlFor="fileUpload"
        ref={dragRef}
      >
        <div> </div>
      </label>
      <div className="DragDrop-Files">
        {files.length > 0 &&
          files.map((file) => {
            const {
              id,
              object: { name },
            } = file;

            return (
              <div key={id}>
                <div>{name}</div>
                <div
                  className="DragDrop-Files-Filter"
                  onClick={() => handleFilterFile(id)}
                  // onClick     .
                >
                  X
                </div>
              </div>
            );
          })}
      </div>
      <Button
        variant="primary"
        type="submit"
        onClick={handleUpload}
        className="mt-3"
      >
        
      </Button>
    </div>
  );
};

export default DragDrop;
